*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 100%;
}

body {
    box-sizing: border-box;
}

ul {
    list-style: none;
}

h1,
h2,
h3,
h4 {
    color: $colorWhite;
    line-height: 1;
}

a {
    color: $colorWhite;
    text-decoration: none;

}

img {
    width: 100%;
}